document.addEventListener('DOMContentLoaded', function() {
    iniciarApp();
});


function iniciarApp() {

    responsiveNav();
    btnScroll();
}

function responsiveNav() {
    const boton = document.querySelector('.barra__menu');

    boton.addEventListener('click', function() {
        const navegacion = document.querySelector('.navegacion');

        navegacion.classList.toggle('no-mostrar');
    });
}

function btnScroll() {
    const btn = document.querySelector('.header__scroll');

    btn.addEventListener('click', function() {
        const main = document.querySelector('.header__scroll');

        main.scrollIntoView();
    });
}

function stickyNav(e) {

    const barraNav = document.querySelector('.barra');

    const scrollValue = window.scrollY;

    if(scrollValue > 250) {
        barraNav.classList.add('scroll');
    } else if (scrollValue < 250) {
        barraNav.classList.remove('scroll');
    }
}

window.addEventListener('scroll', stickyNav);